<template>
  <div>
    <form>
      <div :class="{'form-group':true, 'has-error':errors.has('')}">
        <label></label>
        <select class="form-control" name="" v-model="news.project"  v-validate="'required'">
          <option></option>
          <option></option>
          <option></option>
          <option></option>
          <option></option>
        </select>
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>

      </div>
      <div :class="{'form-group':true, 'has-error':errors.has('')}">
        <label></label>
        <select class="form-control" name="" v-model="news.game"  v-validate="'required'">
        <option v-for="g in games">{{g}}</option>
        </select>
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
      </div>
      <div :class="{'form-group':true, 'has-error':errors.has('')}">
        <label></label>
        <input type="text" class="form-control" placeholder="" v-model="news.title" v-validate="'required'" name="">
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
      </div>
      <div :class="{'form-group':true, 'has-error':errors.has('')}">
        <label></label>
        <select class="form-control" name="" v-model="news.matchName" >
          <option v-for="match in matchList">{{match}}</option>
        </select>
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>

      </div>
      <div :class="{'form-group':true, 'has-error':errors.has('')}" style="width:200px">
        <label>()</label>
        <select class="form-control" name="" v-model="news.matchPreFlag"   v-validate='required' style="width:150px">
          <option value="0"></option>
          <option value="1"></option>
        </select>
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
      </div>
      <div :class="{'form-group':true, 'has-error':errors.has('')}">
        <label></label>
        <input type="text" class="form-control" placeholder="" v-model="news.source" name="">
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
      </div>
      <div :class="{'form-group':true, 'has-error':errors.has('')}">
        <label></label>
        <editor id="editor_id" height="600px" width="100%" :content="news.content"
        pluginsPath="/static/kindeditor/plugins/"
        :loadStyleMode="false"
        @on-content-change="onContentChange"></editor>
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
      </div>
      <div :class="{'form-group':true, 'has-error':errors.has('')}">
        <label></label>
        <input ref="files" type="file" name="image" id="image" v-on:change="upload">
        <img :src="news.image" class="img-responsive"/>
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
      </div>
      <button type="button" class="btn btn-default" v-on:click="submit()"></button>
    </form>
  </div>
</template>

<script>
import g from '../../global.js'
import Vue from 'vue'
import VueResource from 'vue-resource'
import zhcn from 'vee-validate/dist/locale/zh_CN'
import VeeValidate, { Validator } from 'vee-validate'
import VueKindEditor from 'vue-kindeditor'
import 'kindeditor/kindeditor-all.js'
import 'kindeditor/themes/default/default.css'

Vue.use(VueResource)

Validator.localize('zh_CN', zhcn)
Vue.use(VeeValidate)

Vue.config.productionTip = false
Vue.use(VueKindEditor)

export default {
  name: 'Index',
  data () {
    return {
      games: '||||||||||||NBA|CBA|||||||||||||||||||||K|J|||||||||| ||'.split('|'),
      news: {},
      matchList:{}
    }
  },
  methods: {
    submit: function () {
      this.$validator.validateAll().then(result => {
        if (result) {
          let news = this.$data.news
          if (!news.id) {
            Vue.http.post(g.t('/api/admin/newss'), JSON.stringify(news)).then(function (response) {
              location.href = '#/newsList'
            }, function (response) {
              g.toLogin()
            })
          } else {
            Vue.http.put(g.t('/api/admin/newss/' + news.id), JSON.stringify(news)).then(function (response) {
              location.href = '#/newsList'
            }, function (response) {
              g.toLogin()
            })
          }
        }
      })
    },
    upload: function (e) {
      let that = this

      e.preventDefault()
      var files = e.target.files
      var data = new FormData()
      data.append('image', files[0])

      Vue.http.post(g.t('/api/admin/file'), data).then(function (response) {
        that.$set(that.$data.news, 'image', response.bodyText)
      }, function (response) {
        g.toLogin()
      })
    },
    onContentChange (val) {
      this.news.content = val
    }
  },
  created () {
  let that = this;
  Vue.http.get(g.t('/api/admin/matchList')).then(function (response) {

    that.matchList=response.bodyText.split('|')

  })
    let news = this.$data.news
    let newsId = this.$route.params.id
    if (newsId) {
      Vue.http.get(g.t('/api/admin/newss/' + newsId)).then((response) => {
        let newsEntity = response.data
        this.$set(news, 'id', newsEntity.id)
        this.$set(news, 'project', newsEntity.project)
        this.$set(news, 'game', newsEntity.game)
        this.$set(news, 'title', newsEntity.title)
        this.$set(news, 'source', newsEntity.source)
        this.$set(news, 'content', newsEntity.content)
        this.$set(news, 'image', newsEntity.image)
        this.$set(news, 'matchName', newsEntity.matchName)
      this.$set(news, 'matchPreFlag', newsEntity.matchPreFlag)
      }, function (response) {
        g.toLogin()
      })
    }
  }
}
</script>

<style scoped>
body{
  background-color: #CCC;
}
</style>



