<template>
  <div>
    <form>
            <div :class="{'form-group':true, 'has-error':errors.has('')}">
        <label></label>
        <input type="text" class="form-control" placeholder="" v-model="config.ckey" v-validate="'required'" name="">
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
      </div>
      <div :class="{'form-group':true, 'has-error':errors.has('')}">
        <label></label>
        <input type="text" class="form-control" placeholder="" v-model="config.cvalue" v-validate="'required'" name="">
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
      </div>
      <div :class="{'form-group':true, 'has-error':errors.has('')}">
        <label></label>
        <input type="text" class="form-control" placeholder="" v-model="config.cdesc" v-validate="'required'" name="">
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
      </div>
      <button type="button" class="btn btn-default" v-on:click="submit()"></button>
    </form>
  </div>
</template>

<script>
import g from '../../global.js'
import Vue from 'vue'
import VueResource from 'vue-resource'
import zhcn from 'vee-validate/dist/locale/zh_CN'
import VeeValidate, { Validator } from 'vee-validate'

Vue.use(VueResource)

Validator.localize('zh_CN', zhcn)
Vue.use(VeeValidate)

export default {
  name: 'Config',
  data () {
    return {
      config: {}
    }
  },
  methods: {
    submit: function () {
      this.$validator.validateAll().then(result => {
        if (result) {
          let config = this.$data.config
          if (!config.id) {
            Vue.http.post(g.t('/api/admin/configs'), JSON.stringify(config)).then(function (response) {
              g.href('/configList')
            }, function (response) {
              g.toLogin()
            })
          } else {
            Vue.http.put(g.t('/api/admin/configs/' + config.id), JSON.stringify(config)).then(function (response) {
              g.href('/configList')
            }, function (response) {
              g.toLogin()
            })
          }
        }
      })
    }
  },
  created () {
    let config = this.$data.config
    let configId = this.$route.params.id
    if (configId) {
      Vue.http.get(g.t('/api/admin/configs/' + configId)).then((response) => {
        let configEntity = response.data
        this.$set(config, 'id', configEntity.id)
        this.$set(config, 'ckey', configEntity.ckey)
        this.$set(config, 'cvalue', configEntity.cvalue)
        this.$set(config, 'cdesc', configEntity.cdesc)
      }, function (response) {
        g.toLogin()
      })
    }
  }
}
</script>

<style scoped>
body{
  background-color: #CCC;
}
</style>



