<template>
  <div>
    <form>
      <div :class="{'form-group':true, 'has-error':errors.has('')}">
        <label></label>
        <select class="form-control" v-model="ad.type" v-validate="'required'">
            <option></option>
            <option></option>
        </select>
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
      </div>
      <div :class="{'form-group':true, 'has-error':errors.has('')}">
        <label></label>
        <input type="text" class="form-control" placeholder="" v-model="ad.name" v-validate="'required'" name="">
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
      </div>
      <div :class="{'form-group':true, 'has-error':errors.has('')}">
        <label></label>
        <input type="text" class="form-control" placeholder="" v-model="ad.link" v-validate="'required'" name="">
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
      </div>
      <div :class="{'form-group':true, 'has-error':errors.has('')||errors.has('')}" v-if="ad.type==''">
        <label></label>
        <input type="date" class="form-control" placeholder="" v-model="ad.endDateStr" v-validate="'required'" name="">
        <input type="time" class="form-control" placeholder="" v-model="ad.endTimeStr" name="">
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
      </div>
      <div :class="{'form-group':true, 'has-error':errors.has('')}" v-if="ad.type==''">
        <label>||</label>
        <input type="text" class="form-control" placeholder="" v-model="ad.teams" v-validate="'required'" name="">
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
      </div>
      <div :class="{'form-group':true, 'has-error':errors.has('')}" v-if="ad.type==''">
        <label></label>
        <select class="form-control" v-model="ad.important" name="" v-validate="'required'">
          <option value="1"></option>
          <option value="0"></option>
        </select>
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
      </div>
      <div :class="{'form-group':true, 'has-error':errors.has('')}" v-if="ad.type==''">
        <label></label>
        <select class="form-control" v-model="ad.light" name="" v-validate="'required'">
          <option value="1"></option>
          <option value="0"></option>
        </select>
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
      </div>
      <button type="button" class="btn btn-default" v-on:click="submit()"></button>
    </form>
  </div>
</template>

<script>
import g from '../../global.js'
import Vue from 'vue'
import VueResource from 'vue-resource'
import zhcn from 'vee-validate/dist/locale/zh_CN'
import VeeValidate, { Validator } from 'vee-validate'
import VueKindEditor from 'vue-kindeditor'
import 'kindeditor/kindeditor-all-min.js'
import 'kindeditor/themes/default/default.css'

Vue.use(VueResource)

Validator.localize('zh_CN', zhcn)
Vue.use(VeeValidate)

Vue.config.productionTip = false
Vue.use(VueKindEditor)

export default {
  name: 'Index',
  data () {
    return {
      ad: {
        type: '',
        important: 0,
        light: 0
      }
    }
  },
  methods: {
    submit: function () {
      this.$validator.validateAll().then(result => {
        if (result) {
          let ad = this.$data.ad
          if (!ad.id) {
            Vue.http.post(g.t('/api/admin/ads'), JSON.stringify(ad)).then(function (response) {
              g.href('/adList')
            }, function (response) {
              g.toLogin()
            })
          } else {
            Vue.http.put(g.t('/api/admin/ads/' + ad.id), JSON.stringify(ad)).then(function (response) {
              g.href('/adList')
            }, function (response) {
              g.toLogin()
            })
          }
        }
      })
    },
    upload: function (e) {
      let that = this

      e.preventDefault()
      var files = e.target.files
      var data = new FormData()
      data.append('image', files[0])

      Vue.http.post(g.t('/api/admin/file'), data).then(function (response) {
        that.$set(that.$data.ad, 'image', response.bodyText)
      }, function (response) {
        console.log(2)
        console.log(response.bodyText)
      })
    },
    onContentChange (val) {
      this.ad.content = val
    }
  },
  created () {
    let ad = this.$data.ad
    let adId = this.$route.params.id
    if (adId) {
      Vue.http.get(g.t('/api/admin/ads/' + adId)).then((response) => {
        let adEntity = response.data
        this.$set(ad, 'id', adEntity.id)
        this.$set(ad, 'type', adEntity.type)
        this.$set(ad, 'name', adEntity.name)
        this.$set(ad, 'link', adEntity.link)
        this.$set(ad, 'endDateStr', adEntity.endDateStr)
        this.$set(ad, 'endTimeStr', adEntity.endTimeStr)
        this.$set(ad, 'teams', adEntity.teams)
        this.$set(ad, 'important', adEntity.important)
        this.$set(ad, 'light', adEntity.light)
      }, function (response) {
        g.toLogin()
      })
    }
  }
}
</script>

<style scoped>
body{
  background-color: #CCC;
}
</style>



