<template>
  <div>
    <form>
      <div :class="{'form-group':true, 'has-error':errors.has('')}">
        <label></label>
        <input type="text" class="form-control" placeholder="" v-model="video.link" v-validate="'required'" name="" :disabled="video.id">
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
      </div>
      <div :class="{'form-group':true, 'has-error':errors.has('')}">
        <label></label>
        <input type="text" class="form-control" placeholder="" v-model="video.name" v-validate="'required'" name="">
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
      </div>
      <div :class="{'form-group':true, 'has-error':errors.has('')}" v-if="video.id">
        <label></label>
        <input type="text" class="form-control" placeholder="" v-model="video.endedName" v-validate="'required'" name="" disabled>
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
      </div>
      <div :class="{'form-group':true, 'has-error':errors.has('')}">
        <label></label>
        <select class="form-control" v-model="video.type" v-validate="'required'" name="" disabled>
            <option></option>
            <option></option>
            <option></option>
            <option></option>
        </select>
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
      </div>
      <div :class="{'form-group':true, 'has-error':errors.has('')}">
        <label></label>
        <input type="text" class="form-control" placeholder="" v-model="video.project" v-validate="'required'" name="" :disabled="video.id">
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
      </div>
      <div :class="{'form-group':true, 'has-error':errors.has('')}">
        <label></label>
        <input type="text" class="form-control" placeholder="" v-model="video.game" v-validate="'required'" name="" :disabled="video.id">
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
      </div>
      <div :class="{'form-group':true, 'has-error':errors.has('')}">
        <label></label>
        <input ref="files" type="file" name="image" id="image" v-on:change="upload">
        <img :src="video.image" class="img-responsive"/>
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
      </div>
      <button type="button" class="btn btn-default" v-on:click="submit()"></button>
    </form>
  </div>
</template>

<script>
import g from '../../global.js'
import Vue from 'vue'
import VueResource from 'vue-resource'
import zhcn from 'vee-validate/dist/locale/zh_CN'
import VeeValidate, { Validator } from 'vee-validate'
import VueKindEditor from 'vue-kindeditor'
import 'kindeditor/kindeditor-all-min.js'
import 'kindeditor/themes/default/default.css'

Vue.use(VueResource)

Validator.localize('zh_CN', zhcn)
Vue.use(VeeValidate)

Vue.config.productionTip = false
Vue.use(VueKindEditor)

export default {
  name: 'Index',
  data () {
    return {
      video: {
        type: ''
      }
    }
  },
  methods: {
    submit: function () {
      this.$validator.validateAll().then(result => {
        if (result) {
          let video = this.$data.video
          if (!video.id) {
            Vue.http.post(g.t('/api/admin/videos'), JSON.stringify(video)).then(function (response) {
              g.href('/videoList')
            }, function (response) {
              g.toLogin()
            })
          } else {
            Vue.http.put(g.t('/api/admin/videos/' + video.id), JSON.stringify(video)).then(function (response) {
              g.href('/videoList')
            }, function (response) {
              g.toLogin()
            })
          }
        }
      })
    },
    upload: function (e) {
      let that = this

      e.preventDefault()
      var files = e.target.files
      var data = new FormData()
      data.append('image', files[0])

      Vue.http.post(g.t('/api/admin/file'), data).then(function (response) {
        that.$set(that.$data.video, 'image', response.bodyText)
      }, function (response) {
        g.toLogin()
      })
    },
    addLive () {
      this.video.lives.push({'name': '', 'link': ''})
    },
    addAd () {
      this.video.ads.push({'name': '', 'link': '', light: 0})
    }
  },
  created () {
    let video = this.$data.video
    let videoId = this.$route.params.id
    if (videoId) {
      Vue.http.get(g.t('/api/admin/videos/' + videoId)).then((response) => {
        let videoEntity = response.data
        this.$set(video, 'id', videoEntity.id)
        this.$set(video, 'name', videoEntity.name)
        this.$set(video, 'link', videoEntity.link)
        this.$set(video, 'type', videoEntity.type)
        this.$set(video, 'project', videoEntity.project)
        this.$set(video, 'game', videoEntity.game)
        this.$set(video, 'image', videoEntity.image)
        if (videoEntity.ended) {
          this.$set(video, 'endedName', videoEntity.ended.name)
        }
      }, function (response) {
        g.toLogin()
      })
    }
  }
}
</script>

<style scoped>
.liveInput{
  width: 40%
}
</style>



