<template>
  <div>
      <h2></h2>
    <form id="queryForm" class="form-inline" role="form">
      <div class="form-group">
        <label></label>
        <input type="input" class="form-control" name="search_LIKE_issue">
      </div>

      <div class="form-group">
        <label></label>
        <select class="form-control" name="search_EQ_project">
          <option value=""></option>
          <option></option>
          <option></option>
          <option></option>
          <option></option>
          <option></option>
        </select>
      </div>
      <div class="form-group">
        <label></label>
        <select class="form-control" name="search_EQ_game">
          <option value=""></option>
          <option v-for="g in games">{{g}}</option>
        </select>
      </div>

      <div class="form-group">
        <label></label>
        <select class="form-control" name="search_EQ_status">
          <option value=""></option>
          <option  value="INIT"></option>
          <option  value="DOING"></option>
          <option  value="MATCHEND"></option>
          <option  value="DRAWING"></option>
          <option  value="DRAW"></option>
          <option  value="PIEAWARD"></option>
        </select>
      </div>
      <div class="form-group">
        <label></label>
        <input type="input" class="form-control" name="search_LIKE_matchName">
      </div>
      <div class="form-group">
        <label></label>
        <input type="date" class="form-control" name="search_GTE_addTime">
      </div>

      <button type="button" class="btn btn-primary" v-on:click="goto(0)"></button>
      <button type="button" class="btn btn-info" v-on:click="create()">+</button>
    </form>
    <hr>
    <table class="table">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th align="center"></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="a in page.content">
            <td>{{ a.issue }}</td>
            <td>{{ a.matchName }}</td>
            <td>{{ a.playDateStr }}</td>
            <td>{{ a.project }}</td>
            <td>{{ a.game }}</td>
            <td>{{ a.statusDesc }}</td>
            <td>{{ a.partNum }}</td>
            <td>{{ a.awardNum }}</td>
            <td>{{ a.problemNum }}</td>
            <td>{{ a.addTimeStr }}</td>
            <td width="200px">
              <button type="button" class="btn btn-info" v-on:click="edit(a)"></button>
              <button type="button" class="btn btn-danger" v-on:click="remove(a)"></button>
              <button type="button" class="btn btn-info" v-on:click="userEdit(a)"></button>
            </td>
          </tr>
        </tbody>
      </table>
      <nav>
        <ul class="pagination">
          <li><a @click="goto(0)" href="javascript:void(0);">&lt;&lt;</a></li>
          <li v-for="n in page.totalPages" v-if="n > page.number - 12 && n < page.number + 10" :class="{'active':n == page.number + 1}"><a href="javascript:void(0);" v-on:click="goto(n-1)">{{ n }}</a></li>
          <li><a @click="goto(page.totalPages-1)" href="javascript:void(0);">&gt;&gt;</a></li>
        </ul>
      </nav>
  </div>
</template>

<script>
import g from '../../global.js'
import Vue from 'vue'
import VueResource from 'vue-resource'
Vue.use(VueResource)

export default {
  name: 'Index',
  data () {
    return {
      games: '||||||||||||NBA|CBA|||||||||||||||||||||K|J|||||||||| ||'.split('|'),
      page: {
        content: [],
        totalElements: 0,
        last: false,
        totalPages: 0,
        sort: null,
        size: 0,
        number: 0,
        first: false,
        numberOfElements: 0
      }
    }
  },
  methods: {
    create: function () {
      location.href = '#/addIssueForm/'
    },
    edit: function (issue) {
      location.href = '#/updateIssueForm/' + issue.id
    },
    userEdit: function (issue) {
      location.href = '#/userForm/' + issue.id
    },
    remove: function (issue) {
      var that = this
      Vue.http.delete(g.t('/api/admin/issue/' + issue.id)).then(function (response) {

        var issue = that.$data.page.content
        issue.splice(issue.indexOf(issue), 1)
      }, function (response) {
        g.showMessage(response)
      })
    },
    goto: function (page) {
      var formStr = $('#queryForm').serialize()
      var that = this
      Vue.http.get(g.t(`/api/admin/issue/page?${formStr}&page=${page}`)).then(function (response) {
        that.$set(that.$data, 'page', response.data)
      }, function (response) {
        g.toLogin()
      })
    }
  },
  created () {
    this.goto(0)
  }
}
</script>

<style scoped>
</style>



