<template>
  <div>
      <h2></h2>
    <form id="queryForm" class="form-inline" role="form">
      <div class="form-group">
        <label></label>
        <input type="input" class="form-control" name="search_LIKE_title">
      </div>
      <div class="form-group">
        <label></label>
        <select class="form-control" name="search_EQ_project">
          <option value=""></option>
          <option></option>
          <option></option>
          <option></option>
          <option></option>
          <option></option>
        </select>
      </div>
      <div class="form-group">
        <label></label>
        <select class="form-control" name="search_EQ_game">
          <option value=""></option>
          <option v-for="g in games">{{g}}</option>
        </select>
      </div>
      <div class="form-group">
        <label></label>
        <input type="date" class="form-control" name="search_GTE_addTime">
      </div>

      <button type="button" class="btn btn-primary" v-on:click="goto(0)"></button>
      <button type="button" class="btn btn-info" v-on:click="create()"></button>
    </form>
    <hr>
    <table class="table">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="a in page.content">
            <td>{{ a.title }}</td>
            <td>{{ a.project }}</td>
            <td>{{ a.game }}</td>
            <td>{{ a.addTimeStr }}</td>
            <td><a :href="a.source" target="_blank" v-if="a.source!=null"></a></td>
            <td>{{ a.matchName }}</td>
            <td>
              <button type="button" class="btn btn-info" v-on:click="edit(a)"></button>
              <button type="button" class="btn btn-danger" v-on:click="remove(a)"></button>
            </td>
          </tr>
        </tbody>
      </table>
      <nav>
        <ul class="pagination">
          <li><a @click="goto(0)" href="javascript:void(0);">&lt;&lt;</a></li>
          <li v-for="n in page.totalPages" v-if="n > page.number - 12 && n < page.number + 10" :class="{'active':n == page.number + 1}"><a href="javascript:void(0);" v-on:click="goto(n-1)">{{ n }}</a></li>
          <li><a @click="goto(page.totalPages-1)" href="javascript:void(0);">&gt;&gt;</a></li>
        </ul>
      </nav>
  </div>
</template>

<script>
import g from '../../global.js'
import Vue from 'vue'
import VueResource from 'vue-resource'
Vue.use(VueResource)

export default {
  name: 'Index',
  data () {
    return {
      games: '||||||||||||NBA|CBA|||||||||||||||||||||K|J|||||||||| ||'.split('|'),
      page: {
        content: [],
        totalElements: 0,
        last: false,
        totalPages: 0,
        sort: null,
        size: 0,
        number: 0,
        first: false,
        numberOfElements: 0
      }
    }
  },
  methods: {
    create: function () {
      location.href = '#/newsForm/'
    },
    edit: function (news) {
      location.href = '#/newsForm/' + news.id
    },
    remove: function (news) {
      var that = this
      Vue.http.delete(g.t('/api/admin/newss/' + news.id)).then(function (response) {
        var newss = that.$data.page.content
        newss.splice(newss.indexOf(news), 1)
      }, function (response) {
        g.toLogin()
      })
    },
    goto: function (page) {
      var formStr = $('#queryForm').serialize()
      var that = this
      Vue.http.get(g.t(`/api/admin/newss/page?${formStr}&page=${page}`)).then(function (response) {
        that.$set(that.$data, 'page', response.data)
      }, function (response) {
        g.toLogin()
      })
    }
  },
  created () {
    this.goto(0)
  }
}
</script>

<style scoped>
</style>



