<template>
  <div>
    <form>
      <div :class="{'form-group':true, 'has-error':errors.has('')}">
        <label></label>
        <input type="password" class="form-control" placeholder="" v-model="info.oldPassword" name="" v-validate="'required'">
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
      </div>
      <div :class="{'form-group':true, 'has-error':errors.has('')}">
        <label></label>
        <input type="password" class="form-control" placeholder="" v-model="info.newPassword" name="" v-validate="'required'">
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
      </div>
      <div :class="{'form-group':true, 'has-error':errors.has('')}">
        <label></label>
        <input type="password" class="form-control" placeholder="" v-model="info.repeatPassword" name="" v-validate="'required'">
        <span class="help-block" v-show="errors.has('')">{{ errors.first('') }}</span>
      </div>
      <button type="button" class="btn btn-default" v-on:click="submit()"></button>
    </form>
  </div>
</template>

<script>
import g from '../../global.js'
import Vue from 'vue'
import VueResource from 'vue-resource'
import zhcn from 'vee-validate/dist/locale/zh_CN'
import VeeValidate, { Validator } from 'vee-validate'

Vue.use(VueResource)

Validator.localize('zh_CN', zhcn)
Vue.use(VeeValidate)

export default {
  name: 'Index',
  data () {
    return {
      info: {
        oldPassword: '',
        newPassword: '',
        repeatPassword: ''
      }
    }
  },
  methods: {
    submit: function () {
      let info = this.$data.info
      this.$validator.validateAll().then(result => {
        if (result) {
          if (info.newPassword !== info.repeatPassword) {
            alert('')
            return
          }

          Vue.http.post(g.t(`/api/admin/accounts/resetPassword?oldPassword=${info.oldPassword}&newPassword=${info.newPassword}`)).then(function (response) {
            alert('')
          }, function (response) {
            if (response.body.code === 401) {
              alert(response.body.message)
            }
          })
        }
      })
    }
  },
  created () {
  }
}
</script>

<style scoped>
body{
  background-color: #CCC;
}
</style>



